.catalog-filters {
  padding-top: 26px;
  padding-right: 66px;
  width: 309px;
}
.catalog-content {
  min-height: 500px;
}

.hide-filters {
  padding-left: 24px;
  margin-bottom: 18px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  background-image: url(../../img/close.png);
  background-position: left center;
  background-repeat: no-repeat;
}

.catalog-filter {
  border-top: 1px solid #eee;
  &__top {
    padding-top: 31px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__caption {
    display: flex;
    align-items: center;
  }

  &__title {
    margin: 0;
    display: inline-block;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }

  &__quantity {
    min-width: 16px;
    margin-left: 5px;
    transform:translateY(-1px);
  }

  &__toggle {
    position: relative;
    width: 12px;
    height: 12px;
    color: #aaa;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      width: 2px;
      height: 100%;
      background-color: currentColor;
      transform: translateX(-50%);
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      transform: translateY(-50%);
    }
  }
}
